.app-wrapper{
  display: grid;
  max-width: 1200px;
  margin: 0 auto;
  grid-template-areas:
          "h h"
          "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2.5fr 9.5fr;
  font-family: Arial;
}

.app-wrapper-content{
  grid-area: c;
  background-color: #F9F8FC;
  z-index: 1;
}